/* @flow */

import type { Realm } from "../../realm.js";
import { ObjectValue } from "../../values/index.js";

export default function(realm: Realm): ObjectValue {
  let obj = new ObjectValue(realm, realm.intrinsics.ObjectPrototype, "document");

  return obj;
}
